/**
 * 
 */
const regexps:any = {
  NUMBER: '^-?(\\.|(0|\\d+))?(\\.?\\d{0,6})?$'
};
/**
 * 
 * @param type regexps
 * @param val  
 * @param empty 
 */
export default function regexp(type:string | RegExp, val: any, empty:boolean = false) {
  return val === '' && empty ? true : new RegExp(typeof type === 'string' ? regexps[type] : type).test(val);
}
