// 
const firstArr = [
  {
    label:"",
    value:"",
  },{
    label:"",
    value:"-",
  },
  {
    label:"",
    value:"+",
  },
]
const thridArr:any =[
  {
    label:"",
    value:"d",
  },
  {
    label:"",
    value:"w",
  },
  {
    label:"",
    value:"m",
  },
  {
    label:"",
    value:"q",
  },
  {
    label:"",
    value:"y",
  },
]
/**
 * isNow:  label:
*/
const getDateList = (isNow) => {
  const secondArr:any = [];
  const number = isNow ? 2 : 100;
  
  for(let i=1;i< number;i++){
    const nowNumber = i.toString();
    secondArr.push({
      label: nowNumber,
      value: nowNumber
    })
  }
  return [firstArr,secondArr,thridArr]
}



export default getDateList;